import React from "react";

interface SelektorGrupyProps {
    grupaChangeFn: (grupa: string) => void;
}

const GRUPY = [
    "Wynagrodzenia i skadki",
    "Wydatki na zadania statutowe",
    "Dotacje na zadania biece",
    "wiadczenia na rzecz osb fizycznych",
    "Wynagrodzenia finansowane programami UE",
    "Wspfinansowanie wynagrodze programw UE",
    "Wynagrodzenia dla innych programw zagranicznych",
    "Wydatki finansowane programami UE",
    "Wspfinansowanie wydatkw programw UE",
    "Wydatki dla innych programw zagranicznych",
    "Dotacje biece z programw UE",
    "Wspfinansowanie dotacji biecych z programw UE",
    "Dotacje biece z innych programw zagranicznych",
    "wiadczenia dla osb fizycznych finansowane z UE",
    "Wspfinansowane wiadcze dla osb fizycznych z UE",
    "wiadczenia dla osb fizycznych z innych programw zagranicznych",
    "Wypaty z porcze i gwarancji udzielonych przez gmin",
    "Obsuga dugu",
    "Inwestycje ze rodkw gminy",
    "Inwestycje ze rodkw UE",
    "Wspfinansowanie inwestycji ze rodkw UE",
    "Dotacje do inwestycji ze rodkw gminy",
    "Dotacje do inwestycji ze rodkw UE",
    "Wspfinansowanie dotacji do inwestycji ze rodkw UE",
    "Zakup i objcie akcji i udziaw",
    "Wniesienie wkadw do spek prawa handlowego",
]

export class SelektorGrupy extends React.Component<SelektorGrupyProps> {
    render() {
        return <select
            id="SelektorGrupy"
            style={{marginBottom: 10}}
            onChange={ev => this.props.grupaChangeFn(ev.target.value)}>
            {GRUPY.map((name) => (
                <option key={name} value={name}>
                    {name}
                </option>
            ))}
        </select>;
    }
}
