import React from "react";
import ReactJoyride from "react-joyride";

interface Props {
    enabled: boolean;
    finishedCallback: any;
}

export class Tour extends React.Component<Props> {
    static STEPS = [
        {
            target: '.prawa',
            content: 'Generator pozwala Ci stworzy grafik do publikacji w mediach spoecznociowych z danymi z dowolnej gminy',
            disableBeacon: true,
        },
        {
            target: '.acc-ustawienia',
            content: 'Tutaj moesz ustawi logo (podaj link do publicznie dostpnej grafiki), wybra wojewdztwo i gmin a na koniec pobra grafik lub link aby podzieli si projektem z innymi',
        },
        {
            target: '.acc-komentarz',
            content: 'W tym miejscu dodasz komentarz (Twj, eksperta lub polityka) do infografiki',
        },
        {
            target: '.acc-budzet',
            content: 'Tutaj wczysz lub wyczysz pokazywanie moduw dotyczcych budetu danej gminy oraz ustawisz ich opcje',
        },
        {
            target: '.acc-wykresy',
            content: 'A tutaj moesz doda lub usun wykres. Pobierze on automatycznie dane z GUS, albo atwo moesz wklei wasne',
        },
        {
            target: '.start-tour',
            content: 'Chcesz skorzysta z samouczka jeszcze raz? Kliknij tutaj. Klik, klik',
        },
    ]

    render () {
        const { enabled, finishedCallback } = this.props;
        // @ts-ignore
        const callback = data => {
            const { action } = data;
            if (action === "reset" || action === "close") {
                finishedCallback();
            }
            // TODO: Capture close, skip, next / prev action
        };
        return (
            <div className="app">
                <ReactJoyride run={enabled}
                              steps={Tour.STEPS} continuous={true} showSkipButton={true} showProgress={true}
                              callback={callback}
                              styles={{
                                  options: {
                                      primaryColor: '#f9c00c',
                                  },
                                  tooltipContainer: {
                                      textAlign: "justify",
                                  }
                              }}

                              locale={{
                                  next: "Dalej",
                                  last: "Zamknij",
                                  skip: "Pomi",
                                  back: "Wstecz"
                              }}
                />
            </div>)
    }

}